.cyborg-navbar{
    min-height: 100px;
}
.navbar-brand img{
    width:200px;
    height: 150px;
}
.fixed-top{
    background-color: var(--color-dark);
    height: 120px;
    margin-top: -10px;
}
@media (max-width: 768px) {
    .navbar-brand img {
            width: 150px;
            height: 120px;
        }
    .fixed-top {
        margin-top: -25px;
        height: 120px;
    }
}
.navbar-nav{
    background-color: var(--color-dark);
    font-size: 1.5rem;
    gap: 3px;
}
.navbar-collapse {
    margin-top: -20px;
}